<template>
	<v-container>
		<v-app-bar app color="primary" dark height="50">
			<div class="d-flex align-center">
				<v-img
					alt="Pict Picker Logo"
					class="shrink mr-2"
					contain
					:src="require('@/assets/selectImage.png')"
					transition="scale-transition"
					width="40"
				/>
			</div>

			<h2
				class="main-header"
				style="color: white; margin-left: 5px; margin-top: 10px"
			>
				Pic Picker
			</h2>

			<v-spacer></v-spacer>
			<div style="margin: 10px; cursor: pointer" @click="userClick">
				{{ currentUser.id }}
			</div>
			<tooltip text="Sellers on the race">
				<v-badge color="green" :content="sellersLength" overlap>
					<v-icon
						dark
						@click="scoreDialog = true"
						style="margin: 5px; cursor: pointer"
						>mdi-account-tie</v-icon
					>
				</v-badge>
			</tooltip>
			<tooltip text="Go back">
				<v-icon
					dark
					@click="goBack"
					style="margin: 5px; cursor: pointer"
					>mdi-keyboard-backspace</v-icon
				>
			</tooltip>
			<logout>
				<v-icon>mdi-logout</v-icon>
			</logout>
		</v-app-bar>
		<router-view />
		<v-dialog v-model="scoreDialog">
			<scores :user="currentUser" />
		</v-dialog>
	</v-container>
</template>

<script>
import Logout from "@/common/components/Logout.vue";
import db from "@/common/services/firebaseUserService";
import { doc, onSnapshot } from "firebase/firestore";
import { mapActions, mapState } from "vuex";
import Tooltip from "@/common/components/Tooltip.vue";
import Scores from "@/common/components/Scores.vue";
export default {
	components: {
		Logout,
		Tooltip,
		Scores,
	},
	data() {
		return {
			scoreDialog: false,
		};
	},
	computed: {
		...mapState("common", ["currentUser"]),
		showLogout() {
			return this.$router.currentRoute.fullPath === "/user";
		},
		sellersLength() {
			return this.currentUser && this.currentUser.sellers
				? Object.entries(this.currentUser.sellers).length
				: "?";
		},

		// ...mapState("user", ["currentRace"]),
	},
	mounted() {
		this.syncRace();
	},
	methods: {
		...mapActions("user", ["setCurrentRace"]),
		...mapActions("common", ["updateCurrentUser"]),
		goBack() {
			this.$router.back();
		},
		userClick() {
			if (this.$router.currentRoute.fullPath !== "/user")
				this.$router.push("/user");
		},
		syncRace() {
			// if(!this.currentUser || !this.currentUser.id)return;
			const user = doc(db, "/users", "" + this.currentUser.id);
			onSnapshot(user, (sn) => {
				console.log("Current user change detected");
				console.log({ data: sn.data() });
				try {
					let u = sn.data();
					if (!u) {
						console.log("aborted");
						return;
					}
					let d = u.race;
					if (!d) {
						d = [];
					}
					this.updateCurrentUser(u);
					this.setCurrentRace(d);
				} catch (e) {
					this.$toast.error(e.message);
					this.setCurrentRace(null);
				}
			});
		},
	},
};
</script>

